import React from "react";
import { NavLink } from "react-router-dom";
import "../styles/BusinessNavbar.css";
import NotificationDropdown from "./NotificationDropdown"; // Import the NotificationDropdown component

const BusinessNavBar = () => {
  const category = localStorage.getItem("category"); // Get category from localStorage

  return (
    <nav className='business-navbar'>
      <ul>
        <li>
          <NavLink to='/profile' className='nav-link' activeClassName='active'>
            Profile
          </NavLink>
        </li>
        <li>
          <NavLink
            to='/financial'
            className='nav-link'
            activeClassName='active'
          >
            Financial
          </NavLink>
        </li>
        <li>
          <NavLink to='/dates' className='nav-link' activeClassName='active'>
            Dates
          </NavLink>
        </li>
        {category === "Hall" && (
          <li>
            <NavLink
              to='/services'
              className='nav-link'
              activeClassName='active'
            >
              Services
            </NavLink>
          </li>
        )}
        <li>
          <NotificationDropdown />
        </li>
      </ul>
    </nav>
  );
};

export default BusinessNavBar;
