import { useState } from 'react';

const FinancialComponent = () => {
  const [fromDate, setFromDate] = useState('');
  const [toDate, setToDate] = useState('');
  const [financialData, setFinancialData] = useState(null);
  const [reservations, setReservations] = useState([]);

  const userId = localStorage.getItem('userId'); // Get userId from localStorage

  const handleFetchFinancialData = async () => {
    if (fromDate && toDate) {
      // Convert strings to Date objects for comparison
      const from = new Date(fromDate);
      const to = new Date(toDate);

      if (to >= from) {
        try {
          // Fetch financial details
          const financialResponse = await fetch(`/api/financials`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ userId, fromDate, toDate }),
          });

          const financialData = await financialResponse.json();
          setFinancialData(financialData);

          // Fetch reservations for the specified date range
          const reservationsResponse = await fetch(`/api/reservations`, {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({ userId, fromDate, toDate }),
          });

          const reservationsData = await reservationsResponse.json();
          setReservations(reservationsData);
        } catch (error) {
          console.error('Error fetching data:', error);
        }
      } else {
        alert('The "To" date must be greater than or equal to the "From" date.');
      }
    } else {
      alert('Please select both "From" and "To" dates.');
    }
  };

  return (
    <div>
      <h2>Financial Details</h2>
      
      <label>
        From Date:
        <input
          type="date"
          value={fromDate}
          onChange={(e) => setFromDate(e.target.value)}
        />
      </label>
      
      <label>
        To Date:
        <input
          type="date"
          value={toDate}
          onChange={(e) => setToDate(e.target.value)}
          min={fromDate} // Ensures the user can't select a "To" date earlier than the "From" date
        />
      </label>
      
      <button onClick={handleFetchFinancialData}>Get Financial Data</button>
      
      {/* Display Financial Summary */}
      {financialData && (
        <div>
          <h3>Summary</h3>
          <p>Total Price: {financialData.total_price_sum}</p>
          <p>Paid: {financialData.paid_sum}</p>
          <p>Remaining: {financialData.price_remaining_sum}</p>
        </div>
      )}

      {/* Display Reservations */}
      {reservations.length > 0 && (
        <div>
          <h3>Reservations</h3>
          <table>
            <thead>
              <tr>
                <th>First Name</th>
                <th>Last Name</th>
                <th>Email</th>
                <th>Phone Number</th>
                <th>Total Price</th>
                <th>Paid</th>
                <th>Remaining</th>
                <th>Reservation Date</th>
              </tr>
            </thead>
            <tbody>
              {reservations.map((reservation) => (
                <tr key={reservation.id}>
                  <td>{reservation.firstName}</td>
                  <td>{reservation.lastName}</td>
                  <td>{reservation.email}</td>
                  <td>{reservation.phoneNumber}</td>
                  <td>{reservation.total_price}</td>
                  <td>{reservation.paid}</td>
                  <td>{reservation.price_remaining}</td>
                  <td>{reservation.purchase_date}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
};

export default FinancialComponent;
